html {
    box-sizing: border-box;
    
  }
  
  *, ::after, ::before {
    box-sizing: border-box;
  }
  
  .body {
    font-family: 'Montserrat', sans-serif;
    color: white;
    padding: 0 20px;
    margin: 0 auto;
    background-image: url("/src/pic/background.jpg");
    background-position: center;
    height: 100vh;
    background-size: cover;
  }
  
  .form__labelText {
    display: inline-block;
    width: 100%;
    max-width: 180px;
    margin-right: 5px;
  }
  
  .form__field {
    border: 2px solid rgb(92, 158, 228);
    padding: 5px;
    width: 100%;
    max-width: 400px;
    border-radius: 10px;
  }
  
  .form__field:hover {
    background-color: rgb(211, 208, 208);
  }
  
  .form__field:active {
    background-color: rgb(224, 191, 191);
  }
  
  .form__title {
    text-align: center;
    font-size: 25px;
    background-color: rgb(92, 158, 228);
    padding: 5px;
    border: 3px solid black;
    border-radius: 10px;
  }
  
  .form__fieldset {
    border: 4px solid rgb(92, 158, 228);
    padding: 20px;
    border-radius: 20px;
    margin: 40px 0;
    max-width: 700px;
    margin: 0 auto;
  }
  
  .form__result {
    text-decoration: underline 4px solid rgb(16, 9, 44);
    font-size: 25px;
    color: rgb(4, 247, 0);
  }
  
  .form__annotation {
    font-size: 11px;
    text-decoration: underline;
  }